import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'
import shouye from './components/shouye.vue'
import my from './components/my.vue'
import zixun from './components/zixun.vue'
import jieshao from './components/jieshao.vue'
import tel from './components/tel.vue'
Vue.use(Router)
var router = new Router({
	routes:[
		{
			path:'/',
			component:shouye
		},
		{
			path:'/my',
			component:my
		},
		{
			path:'/zixun',
			component:zixun
		},
		{
			path:'/jieshao',
			component:jieshao
		},
		{
			path:'/tel',
			component:tel
		}
	]
})


new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



