<style>
	.my .top{
		width: 100%;
		height: 59px;
		padding: 0 40px;
		background: #000000;
		border-top: 1px solid rgba(68,68,68,1);
		border-bottom: 1px solid rgba(68,68,68,1);
		line-height: 59px;
	}
	.my .top p{
		font-size:14px;
		color:#aaaaaa;
		font-family:微软雅黑;
		margin: 0;
		padding: 0;
	}
	.my .top a{
		text-decoration: none;
		color:#aaaaaa;
	}
	.my .center{
		width: 100%;
		height: auto;
		padding-top: 60px;
		background-color: #222222;
	}
	.my .chahuo{
		max-width: 1150px;
		height: 47px;
		max-height: 47px;
		border: 1px solid rgba(239,193,124,1);
		margin: 0 auto;
		text-align: center;
		line-height: 47px;
		letter-spacing: 1px;
		font-family: times new roman;
		color:#EFC17C;
		font-size:15px;
	}
	.my .bottom{
		width: 100%;
		height: auto;
		padding: 40px 0 150px 0;
		background-color: #222222;
	}
	.my .bottom .bottom_c{
		max-width: 1190px;
		height: auto;
		margin: 0 auto;
	}
	.my .bottom .bottom_c li{
		width: 32%;
		margin: 0 0.6% 3%;
		padding: 6px;
		display: inline-block;
		background: #333333;
		text-align: center;
		cursor: pointer;
	}
	.my .bottom .bottom_c li img{
		width: 100%;
		height: auto;
	}
	.my .bottom .bottom_c li .p{
		font-size: 13px;
	    font-weight: normal;
	    color: #c6a86f;
	    font-family: Raleway;
	    margin-top: 10px;
	}
	.my .bottom .bottom_c li .p1{
		font-size: 16px;
	    font-weight: normal;
	    color: #c6a86f;
	    font-family: Lora;
	}
	/**/
