
<style>
	.zixun{
		width: 100%;
		height: auto;
	}
	.zixun .top{
		width: 100%;
		height: 59px;
		padding: 0 40px;
		background: #000000;
		border-top: 1px solid rgba(68,68,68,1);
		border-bottom: 1px solid rgba(68,68,68,1);
		line-height: 59px;
	}
	.zixun .top p{
		font-size:14px;
		color:#aaaaaa;
		font-family:微软雅黑;
		margin: 0;
		padding: 0;
	}
	.zixun .top a{
		text-decoration: none;
		color:#aaaaaa;
	}
	.zixun .center{
		width: 100%;
		height: auto;
		padding: 40px;
		background: #000000;
		float: left;
	}
	.zixun .center_c{
		max-width: 1200px;
		height: auto;
		margin: 0 auto;
		float: left;
	}
	.zixun .center_c li{
		width: 30%;
		height: 695px;
		background: #262626 none repeat center center;
		float: left;
		margin: 0 1.5% 20px;
	}
	.zixun .center_c li img{
		width: 100%;
		height: auto;
	}
	.zixun .center_c .b{
		width: 100%;
		height: auto;
    	position: relative;
	}
	.zixun .center_c .b p{
		color: #999999;
	    font-size: 16px;
	    font-family: 微软雅黑;
	    font-weight: normal;
		padding: 50px 0 25px 10px;
		margin: 0;
	}
	.zixun .center_c .b .p{
		padding: 10px 10px 40px 10px;
		color: #666666;
		font-size: 13px;
		word-break: break-all;
	    font-family: 微软雅黑;
	    line-height: 20px;
	}
	.zixun .center_c .b .span{
		width: 60px;
		height: 60px;
		border-radius: 50%;
		background: #000000;
		color: #FFFFFF;
		text-align: center;
		line-height: 25px;
		font-size: 12px;
    	font-family: courier new;
    	position: absolute;
    	top: -30px;left: 50%;
    	margin-left: -30px;
	}
	.zixun .center_c .b .span .span1{
		font-size: 12px;
    	font-family: courier new;
    	display: block;
    	margin-top: 5px;
	}
	.zixun .center_c .b .span span{
		font-size: 20px;
    	font-family: courier new;
	}
	/**/
