
<style>
	.tel{
		width: 100%;
		height: auto;
		background: #000000;
		padding: 50px 0;
	}
	.tel .tel_c{
		max-width: 1180px;
		height: auto;
		margin: 0 auto 80px;
		padding: 0;
	}
	.tel .tel_c .l{
		text-align: center;
		padding: 0;
		margin-bottom: 20px;
	}
	.tel .tel_c .l img{
		width: 85%;
		border:1px solid #3d3d3d;
	}
	.tel .tel_c .r h6{
		letter-spacing: 1px;
		font-family: trebuchet ms;
		color:#FFFFFF;
		margin: 0;
		font-size:22px;
	}
	.tel .tel_c .r p{
		font-family: trebuchet ms;
		color:#FFFFFF;
		font-size:20px;
		margin: 0 0 40px 0;
	}
	.tel .tel_c .r span{
		display: block;
		font-family: 微软雅黑;
		color:#808080;
		font-size:14px;
		line-height: 2;
	}
	.tel .tel_c .r .span{
		margin-bottom: 30px;
	}
	.tel .tel_c .r .inp{
		width: 100%;
		height: 50px;
	}
	.tel .tel_c .r .inp input,.neir{
		height: 40px;
		border: 0;
		background: transparent;
		outline: none;
		border-bottom: 1px solid rgb(239, 193, 124);
		margin-bottom: 20px;
	}
	.tel .tel_c .r .neir{
		width: 100%;
		height: 62px;
		padding-left: 15px;
		margin-bottom: 30px;
	}
	.tel .tel_c .r .inp .name{
		width: 48%;
		margin-right: 4%;
	}
	.tel .tel_c .r .inp .youx{
		width: 48%;
	}
	.tel .tel_c .r .fas{
		width: 110px;
		height: 40px;
		font-size: 14px;
		line-height:14px;
		line-height: 40px;
		color: #efc17c;
		padding-left: 40px !important;
		background-color: #000000;
		border: 1px solid #efc17c;
		cursor: pointer;
	}
	/**/
