	.tel .tel_b{
		width: 1180px;
		height: auto;
		margin: 0 auto;
	}
	.tel .tel_b li{
		width: 24%;
		margin: 0 0.5%;
		display: inline-block;
		border: 1px solid rgba(239,193,124,0.3);
	}
	.tel .tel_b li img{
		width: 100%;
	}
